<template>
  <div>
    <!-- 輸入代辦項目 input -->
    <div class="position-relative input-group-lg ">
      <font-awesome-icon class="position-absolute pull-left-icon text-secondary" icon="plus" size="lg" fixed-width />
      <input @keyup.enter="submit" type="text" class="form-control icon-input" placeholder="輸入待辦項目..." v-model="todo.name">
    </div>

    <!-- 代辦list -->
    <div class="todolist-wrap mt-3">
      <half-circle-spinner v-if="!isLoading" :animation-duration="1000" :size="40" :color="'#007bff'" />
      <template v-else v-for="(item, key) in todoGroupList">
        <NTodoGroupRow :todos='item' :ID='key' :key='key' />
      </template>
      <ol v-if="isLoading">
        <li v-for="(item, key, i) in todoGroupList" :key="i">
          index: {{i }},
          key: {{key }},
          <ol>
            <li v-for="(item2, key, i) in item" :key="i">
              {{key}}
              {{item2}}
              {{i}}
            </li>
          </ol>
        </li>
      </ol>
      <!-- <template v-for="item in todoGroup">
        <TodoGroupRow :todo='item' :key='item.id' />
      </template> -->
    </div>

    <!-- 新增按鈕（暫時隱藏）-->
    <div v-if="false" class="fixed-bottom text-center mb-4">
      <router-link :to="{path: `/todoedit/new`}" class="text-primary ">
        <font-awesome-icon icon="plus-circle" style="font-size: 3rem;" />
      </router-link>
    </div>
  </div>
</template>

<script>
