import TodoGroupRow from '@/components/template/todo-group-row.vue'
import NTodoGroupRow from '@/components/template/n-todo-group-row.vue'
import TodoGroupCheckRow from '@/components/template/todo-group-check-row.vue'

import { rtdbDB, rtdbDBAuth } from '@/service/rtdb_db'
import ToDo from '@/model/ToDo'

export default {
  name: 'todoList',
  components: {
    TodoRow,
    TodoGroupRow,
    NTodoGroupRow,
    TodoGroupCheckRow
  },
  firebase: {
    db: rtdbDB.ref('dessert1'),
    root: rtdbDB.ref('/')
  },
  data () {
    const todo = new ToDo()
    return {
      todo,
      todoGroupList: {},
      newItem: {
        name: 'test',
        price: 100,
        id: '101'
      },
      isShowCheckedTodo: false,
      isShowCheckedTodoGroup: false,
      todos: [
        {
          id: 1,
          name: 'buy brackfast',
          dateLine: '2018/06/19',
          group: 'today',
          secondaryTodo: [{
            isCheck: false,
            name: 'double egg hamburger, beef'
          }, {
            isCheck: false,
            name: 'juice'
          }, {
            isCheck: true,
            name: 'toast'
          }, {
            isCheck: true,
            name: 'milk'
          }],
          location: '中山區',
          note: 'note something',
          isCheck: false,
          isImportant: false
        }, {
          id: 2,
          name: 'play',
          dateLine: '2019/06/23',
          group: 'play',
          secondaryTodo: [{
            isCheck: false,
            name: 'double egg hamburger, beef'
          }, {
            isCheck: false,
            name: 'juice'
          }, {
            isCheck: true,
            name: 'toast'
          }, {
            isCheck: true,
            name: 'milk'
          }],
          location: '中山區',
          note: 'note something',
          isCheck: false,
          isImportant: true
        }, {
          id: 3,
          name: 'eat dinner with friends',
          dateLine: '2019/06/29',
          group: 'play',
          secondaryTodo: [{
            isCheck: false,
            name: 'double egg hamburger, beef'
          }, {
            isCheck: false,
            name: 'juice'
          }, {
            isCheck: true,
            name: 'toast'
          }, {
            isCheck: true,
            name: 'milk'
          }],
          location: '中山區',
          note: 'note something',
          isCheck: false,
          isImportant: true
        }, {
          id: 4,
          name: 'buy lunch',
          dateLine: '2018/06/19',
          group: 'today',
          secondaryTodo: [{
            isCheck: false,
            name: 'double egg hamburger, beef'
          }, {
            isCheck: false,
            name: 'juice'
          }, {
            isCheck: true,
            name: 'toast'
          }, {
            isCheck: true,
            name: 'milk'
          }],
          location: '中山區',
          note: 'note something',
          isCheck: false,
          isImportant: false
        }],
      todoGroup: [
        {
          group: 'today',
          id: 10,
          todos: [
            {
              id: 101,
              name: 'buy brackfast',
              dateLine: '2018/06/19',
              group: 'today',
              location: '中山區',
              note: 'note something',
              isCheck: true,
              isImportant: false
            },
            {
              id: 103,
              name: 'work',
              dateLine: '2018/06/19',
              group: 'today',
              location: '中山區',
              note: 'note something',
              isCheck: true,
              isImportant: false
            }, {
              id: 20,
              name: 'buy lunch',
              dateLine: '2018/06/19',
              group: 'today',
              location: '中山區',
              note: 'note something',
              isCheck: false,
              isImportant: false
            }]
        }, {
          group: 'play',
          id: 30,
          todos: [
            {
              id: 301,
              name: 'play with friend',
              dateLine: '2019/06/23',
              group: 'play',
              location: '中山區',
              note: 'note something',
              isCheck: true,
              isImportant: false
            }, {
              id: 40,
              name: 'eat dinner with friends',
              dateLine: '2019/06/29',
              group: 'play',
              location: '中山區',
              note: 'note something',
              isCheck: false,
              isImportant: true
            }]
        }]
    }
  },
  computed: {
    hasIsCheckTodo () {
      return this.todos.filter(item => item.isCheck === true).length > 0
    },
    hasIsCheckTodoGroup () {
      const groupItem = this.todoGroup.map(item => item.todos)
      var element = 0
      for (var index = 0; index < groupItem.length; index++) {
        element += groupItem[index].filter(item => item.isCheck === true).length
      }

      return element > 0
    },
    checkTodoGroupItems () {
      const groupItem = this.todoGroup.map(item => item.todos)
      let checkTodoAry = []
      let checkTodoAryList = []

      for (let index = 0; index < groupItem.length; index++) {
        var item = groupItem[index].filter(item => item.isCheck === true)
        if (item.length > 0) checkTodoAry.push(item)
      }

      for (let index = 0; index < checkTodoAry.length; index++) {
        for (let item = 0; item < checkTodoAry[index].length; item++) {
          checkTodoAryList.push(checkTodoAry[index][item])
        }
      }

      return checkTodoAry
      // return checkTodoAryList
    }
  },
  methods: {
    submit () {
      rtdbDB.ref(`todoList/${this.todo.group}`).push(this.todo)

      this.todo.name = ''
      // this.$firebaseRefs.root.push({
      //   name: this.newItem.name,
      //   price: this.newItem.price,
      //   id: this.newItem.id
      // })
      // this.newItem.name = ''
      // this.newItem.price = ''
      // this.newItem.id = ''
    }
  },
  mounted () {
    rtdbDB.ref('todoList').on('value', snapshot => {
      let data = snapshot.val()
      // for (const key in data) {
      //   if (data.hasOwnProperty(key)) {
      //     const element = data[key]
      //     console.log(element)
      //   }
      // }
      this.todoGroupList = data
      console.log(data)
      this.isLoading = true
    })
  },
  created () {
    console.log(rtdbDBAuth)
  }
}
</script>



