<template>
  <div class="todo-row-wrap p-2 my-3 d-flex align-items-center" :class="{ lock: todo.isCheck }">
    <div class="todo-row-card">
      <div class="form-check pl-3 mb-0">
        <font-awesome-icon class="ml-auto text-dark" size="lg" :icon="changeIconsByCheck(todo.isCheck)" @click="todo.isCheck = !todo.isCheck"/>
        <router-link :to="{path: `/todoEdit/${todo.id}`}" class="text-dark">
          <label class="form-check-label pl-2">
            {{todo.name}}
          </label>
        </router-link>
      </div>
    </div>
    <font-awesome-icon class="ml-auto" :class="{'text-primary':todo.isImportant}" size="lg" :icon="changeIconsByImportant(todo.isImportant)" @click="todo.isImportant = !todo.isImportant"/>
  </div>
</template>

<script>
export default {
  props: {
    todo: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      isImportant: false,
      isCheck: false
    }
  },
  computed: {
    group () {
      return this.todo.group
    }
  },
  methods: {
    changeIconsByImportant (isImportant) {
      if (isImportant) {
        return ['fas', 'star']
      }
      return ['far', 'star']
    },
    changeIconsByCheck (isCheck) {
      if (isCheck) {
        return ['fas', 'check-square']
      }
      return ['far', 'square']
    }
  }
}
</script>

<style>

</style>



