<template>
  <div>
    <router-link :to="{path: `/nTodoGroupList/${ID}`}">
      <button class="btn w-100 text-left p-0 d-flex align-items-center" v-if="!allIsCheck" type="button">
        <label for="dateline" class="col-form-label">
          <font-awesome-icon fixed-width class="ml-auto text-dark" size="lg" icon="folder-open" />
          {{ID}}
        </label>
        <span class="ml-auto badge badge-primary">{{todoTurnArray.length}}</span>
        <font-awesome-icon class="ml-3" size="lg" icon="trash" />
      </button>
    </router-link>
    <hr>
  </div>
</template>

<script>
export default {
  props: {
    todos: {
      type: Object,
      required: true
    },
    ID: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      isImportant: false,
      isCheck: false
    }
  },
  computed: {
    allIsCheck () {
      const isCheckTodos = this.todoTurnArray.filter(item => item.isCheck === true)
      return isCheckTodos.length === this.todoTurnArray.length
    },
    todoTurnArray () {
      return Object.keys(this.todos).map(item => {
        return this.todos[item]
      })
    }
  },
  methods: {
    changeIconsByImportant (isImportant) {
      if (isImportant) {
        return ['fas', 'star']
      }
      return ['far', 'star']
    },
    changeIconsByCheck (isCheck) {
      if (isCheck) {
        return ['fas', 'check-square']
      }
      return ['far', 'square']
    }
  }
}
</script>

<style>
</style>



