import moment from 'moment'

export default class ToDo {
  constructor (json = {}) {
    // this.id = json.id
    this.name = json.name || ''
    this.dateLine = json.date || moment().format('YYYY-MM-DD')
    this.group = json.group || 'today'
    this.secondaryTodo = json.secondaryTodo || []
    this.location = json.location || ''
    this.note = json.note || ''
    this.isCheck = json.isCheck || false
    this.isImportant = json.isImportant || false
  }
}



